import uniPopup from '../uni-popup/uni-popup.vue';
export default {
  components: {
    uniPopup
  },
  props: {
    text: {
      type: String,
      default: '操作成功'
    }
  },
  methods: {
    publish() {
      // 打开 popup 提示框
      this.$refs.popup.open();
      setTimeout(() => {
        this.$refs.popup.close();
      }, 1500);
    }
  }
};
